    background-color: rgba(27, 27, 27, 0.6);
    height: 100%;
    width: 100%; }
    .banner .overlay .contenido-titulo {
      display: flex;
      align-items: flex-end;
      justify-content: flex-start;
      height: 100%; }
      .banner .overlay .contenido-titulo h2 {
        display: flex;
        flex-direction: column;
        text-align: start;
        color: #FFFFFF; }
        .banner .overlay .contenido-titulo h2 span {
