.navegacion {
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: opacity .3s linear, height 0.5s linear;
  display: flex;
  flex-direction: column;
  align-items: center; }
  .navegacion a {
    color: #FFFFFF;
    font-size: 2.4rem;
    font-weight: 400; }
    .navegacion a:hover {
      transform: scale(1.2); }
  @media (min-width: 768px) {
