.carrousel-contenedor {
  position: relative; }
  .carrousel-contenedor .carrousel__anterior, .carrousel-contenedor .carrousel__siguiente {
    z-index: 1;
    position: absolute;
    display: block;
    border: none;
    width: 2.4rem;
    height: 10rem;
    top: calc(30% -1rem);
    cursor: pointer;
    line-height: 3rem;
    text-align: center;
    color: #1b1b1b;
    border-radius: 1rem;
    opacity: 0.4; }
    .carrousel-contenedor .carrousel__anterior:hover, .carrousel-contenedor .carrousel__siguiente:hover {
      opacity: 1; }
  .carrousel-contenedor .carrousel__anterior {
    left: -1rem; }
    @media (min-width: 1024px) {
      .carrousel-contenedor .carrousel__anterior {
        left: -2rem; } }
  .carrousel-contenedor .carrousel__siguiente {
    right: -1rem; }
    @media (min-width: 1024px) {
      .carrousel-contenedor .carrousel__siguiente {
        right: -2rem; } }
  .carrousel-contenedor .carrousel-items {
    overflow: hidden;
    border-radius: 1rem; }
    .carrousel-contenedor .carrousel-items img {
      margin: 1rem;
      max-height: 20rem;
      background-position: center center !important;
      object-fit: contain; }
      @media (min-width: 768px) {
        .carrousel-contenedor .carrousel-items img {
          max-height: 20rem; } }

